import './MidContainer.css';
import Carousel from 'react-bootstrap/Carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faCalendar} from '@fortawesome/free-regular-svg-icons';
import {faFaceGrin} from '@fortawesome/free-regular-svg-icons';
import AnimeStarWars from './animations/AnimeStarWars';

function MidContainer({dataMidContainer,articleMidContainer}){



return (
<div className="midContainer">
<div className='midContainer-movies'>
    <div className='midContainer-movies-up'>
        <div className='midContainer-movies-up-p'><p>Movies</p></div>
        <Carousel  indicators={false} interval={1500}>
   {dataMidContainer.map(item => <Carousel.Item><img
      className="d-block w-100"
      src={item.img}
      alt={item.id}
    /><Carousel.Caption>
    <p>{item.text}</p>
  </Carousel.Caption>
</Carousel.Item> )}
</Carousel>
<AnimeStarWars />
</div>
</div>

<div className='midContainer-titles'>
<div className='midContainer-movies-left-p'><p>See latest News</p></div>
<div className='titles-grid'>
  {articleMidContainer.map(el =>
  <div className='article-grid-item'><img src={el.img}></img>
  <div className='article-grid-item-wrapper'><p>{el.title}</p><div className='divarticle'><p>
  <FontAwesomeIcon icon={faCalendar}  /> {el.date}</p><p> <FontAwesomeIcon icon={faFaceGrin}/>{el.author}</p></div></div>
  </div>
     )}
    </div>
</div>
</div>
);




}
export default MidContainer;