
import React, { useEffect, useState } from 'react';
import menuStyles from './UpperMenu.module.css';
function UpperMenu(){
  const [dataMenus,setdataMenus] = useState([])
  async function dataMenusRequest(){
    let url = `https://starwars-bfaca-default-rtdb.europe-west1.firebasedatabase.app/.json`;
    try{
    const response = await fetch(url);
    let apiData = await response.json();
    return apiData;
    }catch(err){
        console.log(`Problem with request: ${err}`);
    }
    }

   useEffect(()=>{
    dataMenusRequest().then((data) => setdataMenus(data))
   },[])

  
try{
    return (
  
      <nav className={menuStyles.menu}>
      <ol>
          <li className={menuStyles['menu-item']}><a href="#0">{dataMenus.first.name || 'Loading...'}</a></li>
        <li className={menuStyles['menu-item']}><a href="#0">{dataMenus.second.name || 'Loading...'}</a></li>
        
       <img className={menuStyles['menu-item-img']} src='https://i.ibb.co/983kkmh/il-570x-N-1732562586-nifh.webp' ></img>
          
       
        <li className={menuStyles['menu-item']}>
          <a href="#0">{dataMenus.third.name || 'Loading...'}</a>
          <ol className={menuStyles["sub-menu"]}>
           {dataMenus.fourth.subMenu.map(el => <li key={el} className={menuStyles['menu-item']}><a href="#0">{el || 'Loading...'}</a></li>)}
          </ol>
        </li>
        <li className={menuStyles['menu-item']}><a href="#0">{dataMenus.fourth.name || 'Loading...'}</a></li>
        
      </ol>
     
    </nav>

    );
  }catch{
    return(
    <nav className={menuStyles.menu}>
    <ol>
    <li className={menuStyles['menu-item']}><p style={{color:'white'}}>Error loading menu</p></li></ol></nav>
    );
  }
    }
    export default UpperMenu;